<template>
	<!-- :style="{'border-bottom':isIndex ? 'none':'1px solid #f5f5f5'}"-->
	<div class="rj-header">
		<mu-appbar z-depth="2" v-if="!isIndex"  :title="topTitle" color="primary">
	      <mu-button v-if="this.$route.path!='/my'" icon @click="goBack" slot="left">
	        <span class="icon iconfont">&#xe647;</span>
	      </mu-button>
	      <mu-button icon slot="right" v-if="!haveRightBtn">
		    <!--<span v-if="this.$route.path==='/assets'" class="icon iconfont">&#xe603;</span>-->
		  </mu-button>
		  <mu-button @click="goQrCode()" icon slot="right" v-if="this.$route.path==='/home/homeDetails'">
		     <span class="icon iconfont">&#xe65a;</span>
		  </mu-button>
		  <mu-button flat  @click="gallery()" slot="right" v-if="this.$route.path==='/qrCode'">相册</mu-button>
	    </mu-appbar>
	    
	    <mu-paper style="position: fixed;bottom: 100px;right: 20px;width: 40px;height: 40px;text-align: center;" @click="goBackWebview"  v-if="isIndex" class="demo-paper" circle :z-depth="5">
	        <!--<span class="icon iconfont" style="font-size: 26px;color:black;">&#xe647;</span>-->
	    	<p style="font-size: 12px;margin-top:8px;"><<返回App</p>
	    </mu-paper>
	</div>
</template>

<script>
export default {
  name: 'RJheader',
  data(){
  	return{
  		transitionName: 'slide-right'
  	}
  },
  methods:{
  	goBackWebview(){
  		if (!window.plus) return;
  		plus.webview.currentWebview().close(); 
  	},
	goBack(){
     	this.$router.back();
	},
	goQrCode(){
		this.$router.push('/qrCode')
	},
	goToRelease(){
		this.$router.push('/release')
	},
	//选择相册中的二维码并扫描
	gallery(){
		let _this = this;
		if(!window.plus) return;
		plus.gallery.pick( function(path){
    		plus.barcode.scan( path, function (type,result) {
				_this.$store.commit('setQrCodeResult',result.replace(/\"/g, ""))
          		_this.$router.replace('/transferAccounts');
			}, function (error) {
				alert("扫描失败");
			});
	    }, function ( e ) {
	    	console.log( "取消选择图片" );
	    }, {filter:"image"} );
	}
  },
  computed:{
  	topTitle() {
  		if(this.$route.path === '/homeDetails'){
			return this.$store.state.homeDetailsTitle;
		}else{
			return this.$route.meta.title;
		}
    },
    isIndex(){
     	return (this.$route.path === "/home"
     	|| this.$route.path === "/quotation"
     	|| this.$route.path === "/find"
     	|| this.$route.path === "/my");
    },
    haveRightBtn(){
    	return this.$route.path==='/qrCode' || this.$route.path==='/home/homeDetails' || this.$route.path==='/my'
    }
  },
  watch:{
  	'$route' (to, from) {

　　}
  }
}
</script>

<style scoped>
.rj-header{
	position: fixed;
	z-index:99999;
	top: 0;
	width: 100%;
	padding: 0;
}
</style>



