<template>
	<div class="m-footer">
		<mu-container v-if="isIndex" class="footer-container">
	      	<mu-bottom-nav>
				<router-link to="/home" class="mu-bottom-item"><i class="iconfont">&#xe781;</i><span class="mu-bottom-item-text">资产</span></router-link>
		        <router-link to="/quotation" class="mu-bottom-item"><i class="iconfont">&#xe6c0;</i><span class="mu-bottom-item-text">行情</span></router-link>
		        <router-link to="/find" class="mu-bottom-item"><i class="iconfont">&#xe604;</i><span class="mu-bottom-item-text">发现</span></router-link>
		      	<router-link to="/my" class="mu-bottom-item"><i class="iconfont">&#xe601;</i><span class="mu-bottom-item-text">我的</span></router-link>
		     	<!--<mu-bottom-nav-item to="/home" title="首页" icon="home"></mu-bottom-nav-item>
			    <mu-bottom-nav-item to="/quotation" title="行情" icon="swap_vertical_circle"></mu-bottom-nav-item>
			    <mu-bottom-nav-item to="/find" title="发现" icon="explore"></mu-bottom-nav-item>
		      	<mu-bottom-nav-item to="/my" title="我的" icon="account_circle"></mu-bottom-nav-item>-->
    		</mu-bottom-nav>
	    </mu-container>
	</div>
</template>

<script>
export default {
  name: 'mFooter',
  data(){
  	return{
  		transitionName: 'slide-right'
  	}
  },
  methods:{
//	back:function(){
//		if(this.$route.path === "/tradeHome" 
//   	|| this.$route.path === "/myOrder"
//   	|| this.$route.path === "/myRelease"){
//   		this.$router.push('/find')
//   	}else{
//   		this.$router.goBack();	
//   	}
//	}
  },
  computed:{
    isIndex(){
     	return (this.$route.path === "/home"
     	|| this.$route.path === "/quotation"
     	|| this.$route.path === "/find"
     	|| this.$route.path === "/my");
    }
  },
  watch:{
  	'$route' (to, from) {

　　}
  }
}
</script>

<style scoped>
/* 路由激活样式 */
.router-link-active .iconfont,.router-link-active span{ color:#3D6DF4;}
a .iconfont{
	color: #CDCCC9;
}
</style>



