<template>
  <div :style="isIndex?'padding-top:0;':'padding-top:47px;height:100%;background-color:white'">
    <m-header></m-header>
    <transition v-if="$route.meta.keepAlive" name="fade" mode="out-in">
			<keep-alive> 
				<router-view class="child-view"></router-view>
			</keep-alive>
		</transition>
		<transition v-if="!$route.meta.keepAlive" name="fade" mode="out-in">
				<router-view class="child-view"></router-view>
		</transition>
    <m-footer></m-footer>
  </div>
</template>

<script>
import mHeader from '@/components/mHeader'
import mFooter from '@/components/mFooter'
export default {
  name: 'Home',
  components:{
  	'm-footer':mFooter,
  	'm-header':mHeader
  },
  data(){
  	return{
  		transitionName:'fade'
  	}
  },
  methods:{
  	
  },
  computed:{
    isIndex(){
     	return (this.$route.path === "/home"
     	|| this.$route.path === "/quotation"
     	|| this.$route.path === "/find"
     	|| this.$route.path === "/my"
     	);
   },
  },
  mounted(){

  },
  watch:{
  	'$route' (to, from) {
			
　　}
  }
}
</script>

<style scoped>
.home{
	
}
</style>
	



